import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoaderService {

  status = new BehaviorSubject(false);

  constructor() {
  }

  showLoader() {
    this.status.next(true);
  }

  hideLoader() {
    this.status.next(false);
  }
}
