import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef, MatSnackBar } from '@angular/material';
import { HolidaysApiService } from '../../components/holidays/holidays-api.service';
import { Holiday } from '../../../core/interfaces/holiday';
import { SnackBarSuccessComponent } from '../../components/snack-bar-success/snack-bar-success.component';
import { SnackBarErrorComponent } from '../../components/snack-bar-error/snack-bar-error.component';

@Component({
  selector: 'app-save-holiday-dialog',
  templateUrl: './save-holiday-dialog.component.html',
  styleUrls: ['./save-holiday-dialog.component.sass']
})
export class SaveHolidayDialogComponent implements OnInit {

  holidayForm: FormGroup;
  holiday: Holiday;
  objectKeys = Object.keys;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialogRef: MatDialogRef<SaveHolidayDialogComponent>,
    private fb: FormBuilder,
    private holidaysService: HolidaysApiService,
    private snackBar: MatSnackBar,
  ) { }

  ngOnInit() {
    this.holiday = Object.assign({}, this.data);
    this.buildForm();
  }

  private buildForm(): void {
    this.holidayForm = this.fb.group({
      date: [ this.holiday ? this.holiday.date : '' , Validators.required ]
    });
  }

  save() {
    this.holidaysService.addHoliday(this.holidayForm.value)
      .subscribe((res) => {
        this.close(res);
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Holiday is saved', panelClass: 'snack-success' });
      }, err => {
        console.error(err);
        this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Holiday is not saved', panelClass: 'snack-error' });
      });
  }

  update() {
    this.holiday.date = this.holidayForm.get('date').value;

    this.holidaysService.updateHoliday(this.holiday)
      .subscribe((res) => {
        this.close(res);
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Holiday is saved', panelClass: 'snack-success' });
      }, err => {
        console.error(err);
        this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Holiday is not saved', panelClass: 'snack-error' });
      });
  }

  close(res = null) {
    this.dialogRef.close(res);
  }

}
