import { IignoreLabelState, initialIgnoreLabelsState } from '../state/ignore-label.state';
import { EIgnoreLabelActions, IgnoreLabelActions } from '../actions/ignore-label.action';

export const IgnoreLabelReducers = (
  state = initialIgnoreLabelsState,
  action: IgnoreLabelActions
): IignoreLabelState => {
  switch (action.type) {
    case EIgnoreLabelActions.GetIgnoreLabelsSuccess: {
      return {
        ...state,
        ignoreLabels: action.payload,
        error: null,
      };
    }

    case EIgnoreLabelActions.GetIgnoreLabelsError: {
      return {
        ...state,
        ignoreLabels: [],
        error: action.error,
      };
    }

    case EIgnoreLabelActions.ClearIgnoreLabels: {
      return {
        ...state,
        ignoreLabels: [],
        error: null,
      };
    }

    default:
      return state;
  }
};
