import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PivotalTrackerComponent } from './pivotal-tracker.component';
import { ArchivedCyclesComponent } from './archived-cycles/archived-cycles.component';
import { CycleTimeComponent } from './cycle-time/cycle-time.component';
import { ArchivedCycleItemComponent } from './archived-cycle-item/archived-cycle-item.component';
import { CheckTokenGuard } from '../shared/guards/check-token.guard';
import { ReleaseStoriesComponent } from './release-stories/release-stories.component';

const routes: Routes = [
  {
    path: 'pivotal',
    component: PivotalTrackerComponent,
    canActivate: [CheckTokenGuard],
    // canActivateChild: [CheckTokenGuard],
    children: [
      {
        path: 'archived-cycles',
        component: ArchivedCyclesComponent,
      },
      {
        path: 'archived-cycles/:id',
        component: ArchivedCycleItemComponent,
      },
      {
        path: 'cycle-time',
        component: CycleTimeComponent,
      },
      {
        path: 'release/:label',
        component: ReleaseStoriesComponent
      },
      {
        path: '**',
        redirectTo: 'archived-cycles',
        pathMatch: 'full'
      }
    ],
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PivotalTrackerRoutes { }
