import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { Store } from '@ngrx/store';
import { IAppState } from '../state/app.state';
import { EProjectActions, GetProjects, GetProjectsError, GetProjectsSuccess } from '../actions/project.action';
import { Project } from '../../interfaces/project';
import { StoryActivityService } from '../../../features/story-activity/story-activity.service';


@Injectable()
export class ProjectEffects {

  @Effect()
  getProjects$ = this.actions$.pipe(
    ofType<GetProjects>(EProjectActions.GetProjects),
    switchMap((action) => this.storyActivityService.getProjects(action.payload).pipe(
      map( (res: Project[]) => new GetProjectsSuccess(res)),
      catchError( (er: any) => of(new GetProjectsError(er)))
    ))
  );

  constructor(
    private storyActivityService: StoryActivityService,
    private actions$: Actions,
    private store: Store<IAppState>
  ) {}
}
