import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, map, switchMap } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { Store } from '@ngrx/store';
import { IAppState } from '../state/app.state';
import { EWeekdayActions, GetWeekdays, GetWeekdaysSuccess } from '../actions/weekday.action';
import { Weekday } from '../../interfaces/weekday';
import { HolidaysApiService } from '../../../shared/components/holidays/holidays-api.service';
import { HttpErrorResponse } from '@angular/common/http';
import { SnackBarErrorComponent } from '../../../shared/components/snack-bar-error/snack-bar-error.component';
import { MatSnackBar } from '@angular/material';


@Injectable()
export class WeekdayEffects {

  @Effect()
  getWeekdays$ = this.actions$.pipe(
    ofType<GetWeekdays>(EWeekdayActions.GetWeekdays),
    switchMap(() => this.holidaysService.getWeekdays().pipe(
      map( (res: Weekday[]) => new GetWeekdaysSuccess(res)),
      catchError( (error: HttpErrorResponse) => {
        console.error(error);
        this.snackBar.openFromComponent(SnackBarErrorComponent, { data: 'Can not get Holidays', panelClass: 'snack-error' });
        return EMPTY;
      })
    ))
  );

  constructor(
    private holidaysService: HolidaysApiService,
    private snackBar: MatSnackBar,
    private actions$: Actions,
    private store: Store<IAppState>
  ) {}
}
