import { Action } from '@ngrx/store';
import { Release } from '../../interfaces/release';

export enum EReleaseActions {
  GetReleases = '[Release] Get Releases',
  GetReleasesSuccess = '[Release] Get Releases Success',
  GetReleasesError = '[Release] Get Releases Error',
  ClearReleases = '[Release] Clear Releases data',
}

export class GetReleases implements Action {
  public readonly type = EReleaseActions.GetReleases;
}

export class GetReleasesSuccess implements Action {
  public readonly type = EReleaseActions.GetReleasesSuccess;
  constructor(public payload: Release[]) {}
}

export class GetReleasesError implements Action {
  public readonly type = EReleaseActions.GetReleasesError;
  constructor(public error: any) { }
}

export class ClearReleases implements Action {
  public readonly type = EReleaseActions.ClearReleases;
}

export type ReleaseActions = GetReleases | GetReleasesSuccess | GetReleasesError | ClearReleases;
