import { EHolidayActions } from '../actions/holiday.actions';
import { HolidayActions } from '../actions/holiday.actions';
import { IHolidayState, initialHolidayState } from '../state/holiday.state';

export const HolidayReducers = (
  state = initialHolidayState,
  action: HolidayActions
): IHolidayState => {
  switch (action.type) {
    case EHolidayActions.GetHolidaysSuccess: {
      return {
        ...state,
        holidays: action.payload,
        error: null,
      };
    }

    case EHolidayActions.GetHolidaysError: {
      return {
        ...state,
        holidays: [],
        error: action.error,
      };
    }

    case EHolidayActions.ClearHolidays: {
      return {
        ...state,
        holidays: [],
        error: null,
      };
    }

    default:
      return state;
  }
};
