import { createSelector } from '@ngrx/store';

import { IAppState } from '../state/app.state';
import { IProjectState } from '../state/project.state';

const selectProjects = (state: IAppState) => state.projects;

export const selectProjectsList = createSelector(
  selectProjects,
  (state: IProjectState) => state && state.projects
);

export const selectProjectsListError = createSelector(
  selectProjects,
  (state: IProjectState) => state.error
);
