import { EWeekdayActions, WeekdayActions } from '../actions/weekday.action';
import { initialWeekdayState, IWeekdayState } from '../state/weekday.state';

export const WeekdayReducers = (
  state = initialWeekdayState,
  action: WeekdayActions
): IWeekdayState => {
  switch (action.type) {
    case EWeekdayActions.GetWeekdaysSuccess: {
      return {
        ...state,
        weekdays: action.payload,
        error: null,
      };
    }

    case EWeekdayActions.GetWeekdaysError: {
      return {
        ...state,
        weekdays: [],
        error: action.error,
      };
    }

    case EWeekdayActions.ClearWeekdays: {
      return {
        ...state,
        weekdays: [],
        error: null,
      };
    }

    default:
      return state;
  }
};
