import { Component, Input, OnDestroy, OnInit } from '@angular/core';

import { TimeCheckerService } from '../../../core';
import { HolidaysApiService } from './holidays-api.service';
import { takeWhile } from 'rxjs/operators';
import { TokenService } from '../../../core/services/token.service';
import { MatDialog, MatSnackBar } from '@angular/material';
import { SaveHolidayDialogComponent } from '../../dialogs/save-holiday-dialog/save-holiday-dialog.component';
import { ConfirmDialogComponent } from '../../dialogs/confirm-dialog/confirm-dialog.component';
import * as moment from 'moment';
import { Holiday } from '../../../core/interfaces/holiday';
import { Weekday } from '../../../core/interfaces/weekday';
import { SnackBarSuccessComponent } from '../snack-bar-success/snack-bar-success.component';
import { SnackBarErrorComponent } from '../snack-bar-error/snack-bar-error.component';
import { FormArray, FormBuilder } from '@angular/forms';
import { select, Store } from '@ngrx/store';
import { IAppState } from '../../../core/store/state/app.state';
import { GetHolidays, GetHolidaysSuccess } from '../../../core/store/actions/holiday.actions';
import { selectHolidaysList } from '../../../core/store/selectors/holiday.selector';
import { selectWeekdaysList } from '../../../core/store/selectors/weekday.selector';
import { GetWeekdays, GetWeekdaysSuccess } from '../../../core/store/actions/weekday.action';

@Component({
  selector: 'app-holidays',
  templateUrl: './holidays.component.html',
  styleUrls: ['./holidays.component.sass']
})
export class HolidaysComponent implements OnInit, OnDestroy {

  @Input() drawer;

  getHolidaysList$ = this.store.pipe(select(selectHolidaysList));
  holidays: Holiday[] = [];

  getWeekdaysList$ = this.store.pipe(select(selectWeekdaysList));

  formWeekdays = this.fb.group({
    weekdaysArray: this.fb.array([])
  });

  private isAlive = true;

  constructor(
    private timeCheckerService: TimeCheckerService,
    private holidaysService: HolidaysApiService,
    private tokenService: TokenService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private fb: FormBuilder,
    private store: Store<IAppState>,
    ) { }

  ngOnInit() {

    this.getHolidaysList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((res: Holiday[]) => {
        if (res) {
          this.holidays = res;
          this.setHolidays(this.holidays);
        }
      });

    this.getWeekdaysList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((res: Weekday[]) => {
        this.setWorkingWeekdays(res);
        this.fetchForm(res);
      });
  }

  fetchForm(weekdays: Weekday[] ) {
    this.weekdaysFormArray.clear();

    weekdays.forEach((weekday) => {
      this.addWeekDayToForm(weekday);
    });
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  get weekdaysFormArray(): FormArray {
    return this.formWeekdays.get('weekdaysArray') as FormArray;
  }

  addWeekDayToForm(weekday) {
    const weekdayFormGroup = this.fb.group({
      name: [ weekday.name ],
      index: [ weekday.index ],
      checked: [ weekday.checked ],
    });

    this.weekdaysFormArray.insert(this.weekdaysFormArray.length, weekdayFormGroup);
  }

  getWeekdays() {
    this.store.dispatch(new GetWeekdays());
  }

  getHolidays() {
    this.store.dispatch(new GetHolidays());
  }

  setWorkingWeekdays(value: Weekday[]) {
    this.timeCheckerService.setWorkingWeekdays = value.filter( day => day.checked).map( day => day.index);;
  }

  setHolidays(value: Holiday[]) {
    this.timeCheckerService.holidays = value.map( item => moment(item.date).format('MM/DD/YYYY'));
  }

  saveWeekdays() {
    this.holidaysService.updateWeekdays(this.formWeekdays.value.weekdaysArray)
      .subscribe( (res: Weekday[]) => {
        this.setWorkingWeekdays(res);
        this.store.dispatch(new GetWeekdaysSuccess(res));

        this.formWeekdays.markAsPristine();
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Week days are saved', panelClass: 'snack-success' });
      }, error => {
        console.error(error);
        this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Week days are not saved', panelClass: 'snack-error' });
      });
  }

  addHoliday() {
    const dialogRef = this.dialog.open(SaveHolidayDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getHolidays();
      }
    });
  }

  updateHoliday(holiday) {
    const dialogRef = this.dialog.open(SaveHolidayDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
      data: holiday,
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getHolidays();
      }
    });
  }

  deleteHoliday(holiday: Holiday) {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
      data: {
        msg: `Are you sure you want to delete a holiday date '${moment(holiday.date).format('MM/DD/YYYY')}'?`,
        holiday,
      },
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.holidaysService.deleteHoliday(result.holiday._id).subscribe(() => {
          this.holidays = this.holidays.filter((item) => item._id !== holiday._id);
          this.setHolidays(this.holidays);
          this.store.dispatch(new GetHolidaysSuccess(this.holidays));
          this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Holiday is removed', panelClass: 'snack-success' });
        }, error => {
          console.error(error);
          this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Holiday is not removed', panelClass: 'snack-error' });
        });
      }
    });
  }
}
