import { Component, OnDestroy, OnInit } from '@angular/core';
import { debounceTime, takeWhile } from 'rxjs/operators';

import { CycleTimeService } from './cycle-time.service';

import { TokenService } from '../../core/services/token.service';
import { AuthService } from '../../core/services/auth.service';
import { Project } from '../../core/interfaces/project';
import { select, Store } from '@ngrx/store';
import { selectProjectsList } from '../../core/store/selectors/project.selector';
import { IAppState } from '../../core/store/state/app.state';
import { ExcelService } from '../../core/services/excel-service';

import { mockIteration } from './mock-iteration';
import { IIteration, ITokenObj } from '../../core/interfaces/interfaces';


@Component({
  selector: 'app-cycle-time',
  templateUrl: './cycle-time.component.html',
  styleUrls: ['./cycle-time.component.scss']
})
export class CycleTimeComponent implements OnInit, OnDestroy {

  projects = [];
  selectedProjects;
  iterations$: any;
  iterations = [];
  selectedIteration: IIteration;
  activityEnum = this.cycleTimeService.activityEnum;
  tokenObj: ITokenObj;

  getProjectsList$ = this.store.pipe(select(selectProjectsList));

  private isAlive = true;

  constructor(
    private cycleTimeService: CycleTimeService,
    private tokenService: TokenService,
    private authService: AuthService,
    private store: Store<IAppState>,
    private excelService: ExcelService,
  ) { }


  ngOnInit() {
    this.getToken();

    this.selectionIteration(mockIteration);

    this.getProjectsList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((projects: Project[]) => {
        this.projects = projects;
      });
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  saveRaw() {
    this.excelService.exportAsRawFile(this.iterations, 'iterations');
  }

  getToken() {
    this.tokenService.tokenChanged
      .pipe(
        takeWhile(() => this.isAlive),
        debounceTime(0)
      )
      .subscribe((res: ITokenObj) => this.tokenObj = res);
  }

  selectionProject(value: number): void {
    this.selectedProjects = value;
    this.iterations$ = this.cycleTimeService.getIterations(value);

    this.iterations$
      .subscribe((res) => {
        this.iterations = res;
      });
  }

  selectionIteration(iteration) {
    this.selectedIteration = iteration;
  }
}
