import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { TokenService } from '../../../core/services/token.service';
import { IIgnoreLabel } from '../../../core/interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class IgnoreLabelsApiService {

  private url = `${environment.baseURL}/ignore-label`;

  constructor(
    private http: HttpClient,
    private tokenService: TokenService
  ) { }

  getIgnoreLabels(): Observable<IIgnoreLabel> {
    return this.http.get<IIgnoreLabel>(`${this.url}/${this.tokenService.getToken().token}`);
  }

  updateIgnoreLabels(ignoreLabels: string[]): Observable<IIgnoreLabel> {
    return this.http.post<IIgnoreLabel>(`${this.url}/${this.tokenService.getToken().token}`, Array.from(ignoreLabels));
  }
}
