import { Component, Input } from '@angular/core';
import { ExcelService } from '../../../core/services/excel-service';
import { Store } from '@ngrx/store';
import { IAppState } from '../../../core/store/state/app.state';
import { IIteration } from '../../../core/interfaces/interfaces';
import { map, takeWhile } from 'rxjs/operators';
import { CycleTimeService } from '../../../features/cycle-time/cycle-time.service';
import { StoriesBasic } from '../../../core/classes/stories-basic.abstract';
import { ArchivedCyclesService } from '../../../features/archived-cycles/archived-cycles.service';
import { LoaderService } from '../../../core/services/loader';
import { CycleTime } from '../../enums';

@Component({
  selector: 'app-stories',
  templateUrl: './stories.component.html',
  styleUrls: ['./stories.component.sass']
})
export class StoriesComponent extends StoriesBasic {
  @Input() iteration: IIteration;
  exportedExcelFileName = 'Exported Stories list';

  constructor(
    protected excelService: ExcelService,
    protected store: Store<IAppState>,
    private cycleTimeService: CycleTimeService,
    private archivedCyclesService: ArchivedCyclesService,
    private loaderService: LoaderService
  ) {
    super(store, excelService);
  }

  getFilteredItems() {
    return this.ignoreLabelsList$
      .pipe(
        takeWhile(() => this.isAlive),
        map(ignoreLabels => this.cycleTimeService.filterStoriesByLabel(this.iteration.stories, ignoreLabels)),
        map(stories => this.cycleTimeService.parseStories(stories, this.iteration.start))
      );
  }


  archiveReviews() {
    this.loaderService.showLoader();
    const storyIds = this.filteredStories.map(story => Number(story[CycleTime.storyId]));
    const projectId = Number(this.filteredStories[0][CycleTime.projectId]);
    this.archivedCyclesService.archiveReviewsForStories(storyIds, projectId)
      .subscribe(
        reviews => {
          console.log(reviews);
        },
        () => {},
        () => this.loaderService.hideLoader()
      )
  }
}
