import { Injectable, Injector } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, finalize, map } from 'rxjs/operators';
import { forkJoin, Observable, throwError } from 'rxjs';
import { MatDialog } from '@angular/material';

import { LoaderService } from '../core/services/loader';
import { TokenService } from '../core/services/token.service';
import { IgnoreLabelsApiService } from '../shared/components/ignore-labels/ignore-labels-api.service';
import { select, Store } from '@ngrx/store';
import { IAppState } from '../core/store/state/app.state';
import { selectIgnoreLabelsList } from '../core/store/selectors/ignore-label.selector';
import { ErrorDialogComponent } from '../shared/dialogs/error-dialog';

@Injectable()
export class PivotalTrackerService {
  status = 'delivered';
  statuses = ['finished', 'delivered', 'rejected', 'accepted'];
  IterationsSubUrl: string;
  IterationsSecondSubUrl: string;
  URL = 'https://www.pivotaltracker.com/services/v5/projects';
  httpOptions;
  projectId: number;
  ignoreLabels = new Set();

  protected http: HttpClient;
  protected dialog: MatDialog;
  protected loaderService: LoaderService;
  protected tokenService: TokenService;
  protected ignoreLabelsApiService: IgnoreLabelsApiService;

  private ignoreLabelsList$ = this.store.pipe(select(selectIgnoreLabelsList));


  constructor(
    protected injector: Injector,
    protected store: Store<IAppState>,
  ) {
    this.http = injector.get(HttpClient);
    this.dialog = injector.get(MatDialog);
    this.loaderService = injector.get(LoaderService);
    this.tokenService = injector.get(TokenService);
    this.ignoreLabelsApiService = injector.get(IgnoreLabelsApiService);

    this.ignoreLabelsList$
      .subscribe((res: string[]) => {
        this.ignoreLabels = new Set(res);
      });
  }

  setIterationsSubUrl(value) {
    this.IterationsSubUrl = value;
  }

  setIterationsSecondSubUrl(value) {
    this.IterationsSecondSubUrl = value;
  }

  getProjects(userId: string): Observable<any> {
    this.setIdHttpOptions(userId);

    this.loaderService.showLoader();

    return this.http.get<any>(this.URL, this.httpOptions).pipe(
      catchError(() => {
        this.openDialog();

        return throwError('Something bad happened; please try again later.');
      }),
      finalize(() => {
        this.loaderService.hideLoader();
      })
    );
  }

  openDialog(): void {
    this.dialog.open(ErrorDialogComponent);
  }

  getIterations(projectId: number) {
    this.projectId = projectId;

    this.loaderService.showLoader();

    const observables = [this.http.get(this.getIterationsUrl()), this.http.get(this.getIterationsSecondUrl())];

    return forkJoin(observables).pipe(
      map(([firstItems, secondItems]) => {
        const allItems = (secondItems as any[]).concat(firstItems);

        return allItems.reduce((acc, current) => {
          if (!acc.find(item => item.number === current.number)) {
            return acc.concat(current);
          } else {
            return acc;
          }
        }, []);
      }),
      map((iterations: any) => {
        return (iterations as any[]).sort((a, b) => new Date(b.start).getTime() - new Date(a.start).getTime());
      }),
      finalize(() => {
        this.loaderService.hideLoader();
      })
    );
  }

  checkLabels(story): boolean {
    return !story.labels.some((label) => this.ignoreLabels.has(label.name));
  }

  private setIdHttpOptions(token) {
    const value = 'X-TrackerToken';

    this.httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
      responseType: 'json'
    };

    this.httpOptions.headers = this.httpOptions.headers.append(value, token);
  }

  private getIterationsUrl() {
    return `${this.URL}/${this.projectId}${this.IterationsSubUrl}`;
  }

  private getIterationsSecondUrl() {
    return `${this.URL}/${this.projectId}${this.IterationsSecondSubUrl}`;
  }
}
