import { EReleaseActions, ReleaseActions } from '../actions/release.actions';
import { initialReleaseState, IReleaseState } from '../state/release.state';

export const ReleaseReducers = (
  state = initialReleaseState,
  action: ReleaseActions
): IReleaseState => {
  switch (action.type) {
    case EReleaseActions.GetReleasesSuccess: {
      return {
        ...state,
        releases: action.payload,
        error: null,
      };
    }

    case EReleaseActions.GetReleasesError: {
      return {
        ...state,
        releases: [],
        error: action.error,
      };
    }

    case EReleaseActions.ClearReleases: {
      return {
        ...state,
        releases: [],
        error: null,
      };
    }

    default:
      return state;
  }
};
