import { Component, OnInit } from '@angular/core';
import { CycleTimeService } from '../cycle-time/cycle-time.service';
import { ArchivedCyclesService } from '../archived-cycles/archived-cycles.service';
import { ActivatedRoute } from '@angular/router';
import { IIteration } from '../../core/interfaces/interfaces';

@Component({
  selector: 'app-archived-cycle-item',
  templateUrl: './archived-cycle-item.component.html',
  styleUrls: ['./archived-cycle-item.component.sass']
})
export class ArchivedCycleItemComponent implements OnInit {
  iteration: IIteration;

  constructor(
    private cycleTimeService: CycleTimeService,
    private archivedCyclesService: ArchivedCyclesService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    // добавить гет реквест на ревью для каждой стори
    const id = this.route.snapshot.paramMap.get('id');
    this.archivedCyclesService.getById(id)
      .subscribe(item => {
        this.iteration = item;
      });
  }
}
