import { select, Store } from '@ngrx/store';
import { selectIgnoreLabelsList } from '../store/selectors/ignore-label.selector';
import { IAppState } from '../store/state/app.state';
import { OnDestroy, OnInit } from '@angular/core';
import { CycleTime } from '../../shared/enums';
import { ExcelService } from '../services/excel-service';
import { Observable } from 'rxjs';

export abstract class StoriesBasic implements OnDestroy, OnInit {
  abstract exportedExcelFileName: string;
  abstract getFilteredItems(): Observable<{[key: string]: string}[]>;

  protected filteredStories = [];
  protected cycleTimeEnum = CycleTime;
  protected isAlive = true;
  protected ignoreLabelsList$ = this.store.pipe(select(selectIgnoreLabelsList));
  protected activityList = [
    CycleTime.projectId,
    CycleTime.storyId,
    CycleTime.storyType,
    CycleTime.storyName,
    CycleTime.labels,
    CycleTime.owner,
    CycleTime.deliveredTime,
    CycleTime.deliveredCount,
    CycleTime.qualityAssurance,
    CycleTime.release,
    CycleTime.comment
  ];

  protected constructor(
    protected store: Store<IAppState>,
    protected excelService: ExcelService
  ) {}

  ngOnInit() {
    this.getFilteredItems().subscribe(stories => this.filteredStories = stories);
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  exportAsExcelFile(data) {
    this.excelService.exportAsExcelFile(data, this.exportedExcelFileName);
  }
}
