import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatSnackBar } from '@angular/material';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReleaseApiService } from '../../components/releases/release-api.service';
import { Release } from '../../../core/interfaces/release';
import { SnackBarSuccessComponent } from '../../components/snack-bar-success/snack-bar-success.component';
import { SnackBarErrorComponent } from '../../components/snack-bar-error/snack-bar-error.component';

@Component({
  selector: 'app-create-release-dialog',
  templateUrl: './save-release-dialog.component.html',
  styleUrls: ['./save-release-dialog.component.sass']
})
export class SaveReleaseDialogComponent implements OnInit {

  releaseForm: FormGroup;
  release: Release;
  objectKeys = Object.keys;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialogRef: MatDialogRef<SaveReleaseDialogComponent>,
    private fb: FormBuilder,
    private releaseService: ReleaseApiService,
    private snackBar: MatSnackBar,
    ) { }

  ngOnInit() {
    this.release = Object.assign({}, this.data);
    this.buildForm();
  }

  private buildForm(): void {
    this.releaseForm = this.fb.group({
      name: [ this.release ? this.release.name : '', Validators.required ],
      date: [ this.release ? this.release.date : '' , Validators.required ]
    });
  }

  save() {
    this.releaseService.addReleases(this.releaseForm.value)
      .subscribe((res) => {
        this.close(res);
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Release is saved', panelClass: 'snack-success' });
      }, err => {
        console.error(err);
        this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Release is not saved', panelClass: 'snack-error' });
      });
  }

  update() {
    this.release.name = this.releaseForm.get('name').value;
    this.release.date = this.releaseForm.get('date').value;

    this.releaseService.updateReleases(this.release)
      .subscribe((res) => {
        this.close(res);
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Release is saved', panelClass: 'snack-success' });
      }, err => {
        console.error(err);
        this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Release is not saved', panelClass: 'snack-error' });
      });
  }

  close(res = null) {
    this.dialogRef.close(res);
  }

}
