import { Action } from '@ngrx/store';

export enum EIgnoreLabelActions {
  GetIgnoreLabels = '[IgnoreLabels] Get IgnoreLabels',
  GetIgnoreLabelsSuccess = '[IgnoreLabels] Get IgnoreLabels Success',
  GetIgnoreLabelsError = '[IgnoreLabels] Get IgnoreLabels Error',
  ClearIgnoreLabels = '[IgnoreLabels] Clear IgnoreLabels data',
}

export class GetIgnoreLabels implements Action {
  public readonly type = EIgnoreLabelActions.GetIgnoreLabels;
}

export class GetIgnoreLabelsSuccess implements Action {
  public readonly type = EIgnoreLabelActions.GetIgnoreLabelsSuccess;
  constructor(public payload: string[]) {}
}

export class GetIgnoreLabelsError implements Action {
  public readonly type = EIgnoreLabelActions.GetIgnoreLabelsError;
  constructor(public error: any) { }
}

export class ClearIgnoreLabels implements Action {
  public readonly type = EIgnoreLabelActions.ClearIgnoreLabels;
}




export type IgnoreLabelActions = GetIgnoreLabels | GetIgnoreLabelsSuccess | GetIgnoreLabelsError | ClearIgnoreLabels;
