import { Action } from '@ngrx/store';

import { Holiday } from '../../interfaces/holiday';

export enum EHolidayActions {
  GetHolidays = '[Holiday] Get Holidays',
  GetHolidaysSuccess = '[Holiday] Get Holidays Success',
  GetHolidaysError = '[Holiday] Get Holidays Error',
  ClearHolidays = '[Holiday] Clear Holidays data',
}

export class GetHolidays implements Action {
  public readonly type = EHolidayActions.GetHolidays;
}

export class GetHolidaysSuccess implements Action {
  public readonly type = EHolidayActions.GetHolidaysSuccess;
  constructor(public payload: Holiday[]) {}
}

export class GetHolidaysError implements Action {
  public readonly type = EHolidayActions.GetHolidaysError;
  constructor(public error: any) { }
}

export class ClearHolidays implements Action {
  public readonly type = EHolidayActions.ClearHolidays;
}



export type HolidayActions = GetHolidays | GetHolidaysError | GetHolidaysSuccess | ClearHolidays;
