import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { TokenService } from '../services/token.service';


@Injectable()
export class HttpConfigInterceptor implements HttpInterceptor {

  constructor(private tokenService: TokenService) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    const currentTokenObj = this.tokenService.currentTokenValue;

    if (currentTokenObj && currentTokenObj.token && !request.url.includes('pivotaltracker.com')) {
      request = request.clone({
        setHeaders: {
          'x-token': `${currentTokenObj._id}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Headers': 'x-token',
          'Content-Type': 'application/json',
        }
      });
    }

    if (currentTokenObj && currentTokenObj.token && request.url.includes('pivotaltracker.com')) {
      request = request.clone({
        setHeaders: {
          'X-TrackerToken': `${currentTokenObj.token}`,
          'Content-Type': 'application/json',
        },
        responseType: 'json',
      });
    }

    return next.handle(request);
  }
}
