import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { TimeCheckerService } from '../../../core/services/time-checker';
import { MatDialog, MatSnackBar } from '@angular/material';
import { SaveReleaseDialogComponent } from '../../dialogs/save-release-dialog/save-release-dialog.component';
import { ReleaseApiService } from './release-api.service';
import { ConfirmDialogComponent } from '../../dialogs/confirm-dialog/confirm-dialog.component';
import { filter, takeWhile } from 'rxjs/operators';
import { Release } from '../../../core/interfaces/release';
import { SnackBarErrorComponent } from '../snack-bar-error/snack-bar-error.component';
import { SnackBarSuccessComponent } from '../snack-bar-success/snack-bar-success.component';
import { select, Store } from '@ngrx/store';
import { IAppState } from '../../../core/store/state/app.state';
import { selectReleasesList, selectReleasesListError } from '../../../core/store/selectors/release.selector';
import { HttpErrorResponse } from '@angular/common/http';
import { GetReleases, GetReleasesSuccess } from '../../../core/store/actions/release.actions';

@Component({
  selector: 'app-releases',
  templateUrl: './releases.component.html',
  styleUrls: ['./releases.component.sass'],
})
export class ReleasesComponent implements OnInit, OnDestroy {

  @Input() drawer;

  getReleasesList$ = this.store.pipe(select(selectReleasesList));
  getReleasesListError$ = this.store.pipe(select(selectReleasesListError));
  releases: Release[] = [];

  private isAlive = true;

  constructor(
    private fb: FormBuilder,
    private timeCheckerService: TimeCheckerService,
    private releasesService: ReleaseApiService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private store: Store<IAppState>,
  ) { }

  ngOnInit() {

    this.getReleasesList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((res: Release[]) => {
        this.releases = res;
      });

    this.getReleasesListError$
      .pipe(
        takeWhile(() => this.isAlive),
        filter(Boolean)
      )
      .subscribe((res: HttpErrorResponse) => {
        console.error(res);
        this.snackBar.openFromComponent(SnackBarErrorComponent, { data: 'Can not get Releases', panelClass: 'snack-error' });
      });
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  getReleases() {
    this.store.dispatch(new GetReleases());
  }

  addRelease(): void {
    const dialogRef = this.dialog.open(SaveReleaseDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getReleases();
      }
    });
  }

  update(release: Release) {
    const dialogRef = this.dialog.open(SaveReleaseDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
      data: release,
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getReleases();
      }
    });
  }

  delete(release: Release) {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      autoFocus: false,
      restoreFocus: false,
      data: {
        msg: `Are you sure you want to delete a release named '${release.name}'?`,
        release,
      },
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.releasesService.deleteReleases(result.release._id).subscribe(() => {
          this.releases = this.releases.filter((item) => item._id !== release._id);
          this.store.dispatch(new GetReleasesSuccess(this.releases));
          this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Release is removed', panelClass: 'snack-success' });
        }, error => {
          console.error(error);
          this.snackBar.openFromComponent(SnackBarErrorComponent,  { data: 'Release is not removed', panelClass: 'snack-error' });
        });
      }
    });
  }

}
