import { Component } from '@angular/core';
import { ArchivedCyclesService } from '../archived-cycles/archived-cycles.service';
import { ActivatedRoute } from '@angular/router';
import { map, takeWhile } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { IAppState } from '../../core/store/state/app.state';
import { CycleTimeService } from '../cycle-time/cycle-time.service';
import { combineLatest } from 'rxjs';
import { ExcelService } from '../../core/services/excel-service';
import { StoriesBasic } from '../../core/classes/stories-basic.abstract';

@Component({
  selector: 'app-release-stories',
  templateUrl: './release-stories.component.html',
  styleUrls: ['./release-stories.component.sass']
})
export class ReleaseStoriesComponent extends StoriesBasic {
  exportedExcelFileName: string;

  constructor(
    private archivedCyclesService: ArchivedCyclesService,
    private route: ActivatedRoute,
    private cycleTimeService: CycleTimeService,
    protected excelService: ExcelService,
    protected store: Store<IAppState>
  ) {
    super(store, excelService);
  }

  getFilteredItems() {
    const releaseLabel = this.route.snapshot.paramMap.get('label');
    this.exportedExcelFileName = `Release - ${this.route.snapshot.paramMap.get('label')} stories`;

    return combineLatest(this.ignoreLabelsList$, this.archivedCyclesService.getStoriesByReleaseLabel(releaseLabel))
      .pipe(
        takeWhile(() => this.isAlive),
        map(([ignoreLabels, stories]) => this.cycleTimeService.filterStoriesByLabel(stories, ignoreLabels)),
        map(stories => this.cycleTimeService.parseReleaseStory(<any> stories))
      );
  }
}
