import { Injectable, Injector } from '@angular/core';
import { DatePipe } from '@angular/common';
import { finalize, map, switchMap } from 'rxjs/operators';
import { forkJoin } from 'rxjs';

import { TimeCheckerService } from '../../core';
import { PivotalTrackerService } from '../pivotal-tracker.service';
import { Store } from '@ngrx/store';
import { IAppState } from '../../core/store/state/app.state';
import { PreviousStatusEnum, StoryActivity } from '../../shared/enums';
import { Activity } from '../../shared/models';

@Injectable({
  providedIn: 'root'
})
export class StoryActivityService extends PivotalTrackerService {
  activityEnum = StoryActivity;

  constructor(
    private timeCheckerService: TimeCheckerService,
    private datePipe: DatePipe,
    protected injector: Injector,
    protected store: Store<IAppState>,
  ) {
    super(injector, store);

    super.setIterationsSubUrl('/iterations?limit=50&offset=-49&scope=done_current');
    super.setIterationsSecondSubUrl('/iterations?limit=50&offset=-99&scope=done_current');
  }

  getStories(stories) {
    const observables = stories.filter((story) => this.checkLabels(story)).map(story => this.getAllActivities(story));

    return forkJoin(observables)
      .pipe(map((activities) => activities.filter((activity: any) => {
          return activity[this.activityEnum.reopenCount] && activity[this.activityEnum.closedDate] !== 'n/a';
        })
      ),
    );
  }



  private getAllActivities(story) {
    this.loaderService.showLoader();

    return this.http
      .get<Activity[]>(`${this.URL}/${this.projectId}/stories/${story.id}/activity?limit=500&offset=0&sort_order=asc`)
      .pipe(
        map((activities: any) => (activities as Activity[])
          .filter((activity: Activity) => (activity.highlight === this.status || activity.highlight === PreviousStatusEnum[this.status]))),
        map((activities: any) => (activities as Activity[])
          .map((activity) => this.filterChanges(activity))),
        map((activities: any[]) => {
          const changes: any[] = [];

          activities.forEach((item: any) => {
            changes.push(...item);
          });

          return changes;
        }),
        map((activities: Activity[]) => this.createActivity(activities, story)),
        switchMap((activities) => this.getOwner(activities)),
        finalize(() => {
          this.loaderService.hideLoader();
        })
      );
  }

  private createActivity(changes: Activity[], story) {
    const newDates = this.getUpdatedDates(changes, this.status);
    const ollDates = this.getUpdatedDates(changes, PreviousStatusEnum[this.status]);
    const topDate: any = this.timeCheckerService.getMaxDate(newDates);
    const bottomDate: any = this.timeCheckerService.getMinDate(ollDates);
    let diff = null;
    let averageTime = null;

    if (ollDates.length) {
      diff = this.timeCheckerService.getDiffFullTime(bottomDate, topDate) || 0;
      averageTime = diff / newDates.length;
    }

    return {
      [this.activityEnum.projectId]: story.project_id.toString(),
      [this.activityEnum.storyId]: story.id.toString(),
      [this.activityEnum.storyType]: story.story_type.toString(),
      [this.activityEnum.storyName]: story.name,
      [this.activityEnum.storyUrl]: story.url,
      [this.activityEnum.labels]: story.labels.map((item) => item.name).join(', ') || 'n/a',
      [this.activityEnum.owner]: '',
      [this.activityEnum.createdDate]: this.getCreatedDate(story),
      [this.activityEnum.closedDate]: this.datePipe.transform(story.accepted_at, 'medium') || 'n/a',
      [this.activityEnum.finishedTime]: diff || 'n/a',
      [this.activityEnum.reopenCount]: newDates.length,
      [this.activityEnum.averageTime]: Math.round(averageTime * 100) / 100 || 0,
      [this.activityEnum.comment]: ''
    };
  }

  private getCreatedDate(story) {
    let result = 'n/a';

    story.labels.forEach((label) => {
      if (label.name.search(/[0-9]{2}r[0-9]{2}$/) === 0) {
        result = label.name;
      }
    });

    return result;
  }
  private getUpdatedDates(changes: any[], status: string) {
    return changes
      .filter((item: any) => item.new_values.current_state === status)
      .map((item: any) => item.new_values.updated_at)
      .filter(item => item);
  }

  private getAllDiffTimes(changes: any[]): number[] {
    const result: number[] = [];

    changes.forEach((change) => {
      const startedDate = change.original_values.updated_at;
      const finishedDate = change.new_values.updated_at;

      result.push(this.timeCheckerService.getDiffFullTime(startedDate, finishedDate));
    });

    return result;
  }

  private filterChanges(activity): Activity {
    return activity.changes.filter((item) => item.kind === 'story' && item.change_type === 'update');
  }

  private getOwner(activities) {
    const projectId: string = activities[this.activityEnum.projectId];
    const storyId: string = activities[this.activityEnum.storyId];

    this.loaderService.showLoader();

    return this.http.get(`${this.URL}/${projectId}/stories/${storyId}/owners`)
      .pipe(
        map((value: any) => {
          const owners = [];

          (value as any[]).forEach((elem: any, index) => {
            if (index < 2) {
              owners.push(elem.name);
            }
          });

          activities[this.activityEnum.owner] = owners.join(', ') || 'n/a';

          return activities;
        }),
        finalize(() => {
          this.loaderService.hideLoader();
        })
      );
  }
}
