import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, filter, map, pluck, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { Store } from '@ngrx/store';
import { IAppState } from '../state/app.state';
import {
  EIgnoreLabelActions,
  GetIgnoreLabels,
  GetIgnoreLabelsError,
  GetIgnoreLabelsSuccess
} from '../actions/ignore-label.action';
import { IgnoreLabelsApiService } from '../../../shared/components/ignore-labels/ignore-labels-api.service';

@Injectable()
export class IgnoreLabelEffects {

  @Effect()
  getIgnoreLabels$ = this.actions$.pipe(
    ofType<GetIgnoreLabels>(EIgnoreLabelActions.GetIgnoreLabels),
    switchMap(() => this.ignoreLabelsApiService.getIgnoreLabels()),
    filter(Boolean),
    pluck('labels'),
    map((res: string[]) => res.filter(Boolean)),
    map( (res: string[]) => new GetIgnoreLabelsSuccess(res)),
    catchError( (er: any) => of(new GetIgnoreLabelsError(er)))
  );

  constructor(
    private ignoreLabelsApiService: IgnoreLabelsApiService,
    private actions$: Actions,
    private store: Store<IAppState>
  ) {}
}
