import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { Release } from '../../../core/interfaces/release';

@Injectable({
  providedIn: 'root'
})
export class ReleaseApiService {

  private url = `${environment.baseURL}/release`;

  constructor(
    private http: HttpClient,
  ) { }

  getReleases(): Observable<any> {
    return this.http.get<any>(this.url);
  }

  addReleases(release: Release): Observable<any> {
    return this.http.post<any>(this.url, release);
  }

  updateReleases(release: Release): Observable<any> {
    return this.http.put<any>(this.url, release);
  }

  deleteReleases(releaseId: string): Observable<any> {
    return this.http.delete<any>(`${this.url}/${releaseId}`);
  }
}
