import { EProjectActions, ProjectActions } from '../actions/project.action';
import { initialProjectState, IProjectState } from '../state/project.state';

export const ProjectReducers = (
  state = initialProjectState,
  action: ProjectActions
): IProjectState => {
  switch (action.type) {
    case EProjectActions.GetProjectsSuccess: {
      return {
        ...state,
        projects: action.payload,
        error: null,
      };
    }

    case EProjectActions.GetProjectsError: {
      return {
        ...state,
        projects: [],
        error: action.error,
      };
    }

    case EProjectActions.ClearProjects: {
      return {
        ...state,
        projects: [],
        error: null,
      };
    }

    default:
      return state;
  }
};
