import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatChipInputEvent, MatSnackBar } from '@angular/material';
import { StoryActivityService } from '../../../features/story-activity/story-activity.service';
import { IgnoreLabelsApiService } from './ignore-labels-api.service';
import { select, Store } from '@ngrx/store';
import {
  selectIgnoreLabelsList,
  selectIgnoreLabelsListError
} from '../../../core/store/selectors/ignore-label.selector';
import { IAppState } from '../../../core/store/state/app.state';
import { filter, takeWhile } from 'rxjs/operators';
import { GetIgnoreLabelsSuccess } from '../../../core/store/actions/ignore-label.action';
import { SnackBarSuccessComponent } from '../snack-bar-success/snack-bar-success.component';
import { HttpErrorResponse } from '@angular/common/http';
import { SnackBarErrorComponent } from '../snack-bar-error/snack-bar-error.component';
import { TokenService } from '../../../core/services/token.service';
import { ITokenObj } from '../../../core/interfaces/interfaces';

const ENTER_KEY = 13;
const COMMA_KEY = 188;

@Component({
  selector: 'app-ignore-labels',
  templateUrl: './ignore-labels.component.html',
  styleUrls: ['./ignore-labels.component.sass']
})
export class IgnoreLabelsComponent implements OnInit, OnDestroy {

  readonly separatorKeysCodes: number[] = [ENTER_KEY, COMMA_KEY];

  ignoreLabels = new Set(['no qa', 'no qa needed']);

  getIgnoreLabelsList$ = this.store.pipe(select(selectIgnoreLabelsList));
  getIgnoreLabelsListError$ = this.store.pipe(select(selectIgnoreLabelsListError));

  tokenObj: ITokenObj;

  private isAlive = true;

  constructor(
    private storyActivityService: StoryActivityService,
    private ignoreLabelsApiService: IgnoreLabelsApiService,
    private store: Store<IAppState>,
    private snackBar: MatSnackBar,
    private tokenService: TokenService,
  ) { }

  ngOnInit() {
    this.getIgnoreLabelsList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((res: string[]) => {
        this.ignoreLabels = new Set(res);
      });

    this.getIgnoreLabelsListError$
      .pipe(
        takeWhile(() => this.isAlive),
        filter(Boolean)
      )
      .subscribe((error: HttpErrorResponse) => {
        console.error(error);
        this.snackBar.openFromComponent(SnackBarErrorComponent, { data: 'Labels did not get', panelClass: 'snack-error' });
      });

    this.tokenService.tokenChanged
      .pipe(takeWhile(() => this.isAlive))
      .subscribe(token => this.tokenObj = token);
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value.trim();

    if (!value) {
      return;
    }

    if (this.ignoreLabels.has(value)) {
      this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'This Labels exist already', panelClass: 'snack-info' });
      return;
    }

    input.value = '';
    this.ignoreLabels.add(value);
    this.updateIgnoreLabels();
  }

  remove(value) {
    this.ignoreLabels.delete(value);
    this.updateIgnoreLabels();
  }

  private updateIgnoreLabels() {
    this.ignoreLabelsApiService.updateIgnoreLabels(Array.from(this.ignoreLabels))
      .subscribe(res => {
        const { labels } = res;
        this.ignoreLabels = new Set(labels);
        this.store.dispatch(new GetIgnoreLabelsSuccess(labels));
        this.snackBar.openFromComponent(SnackBarSuccessComponent,  { data: 'Labels are saved', panelClass: 'snack-success' });
      });
  }
}
