import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'timeActivities'
})
export class TimeActivitiesPipe implements PipeTransform {

  constructor(private datePipe: DatePipe) {
  }

  transform(iteration: any, args?: any): any {

    const startDate = this.datePipe.transform(iteration.start, 'mediumDate');
    const finishDate = this.datePipe.transform(iteration.finish, 'mediumDate');

    return `Start: ${startDate}, Finish: ${finishDate}`;
  }

}
