import { NgModule } from '@angular/core';

import { TimeCheckerService } from './services/time-checker';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { appReducers } from './store/reducers/app.reducers';
import { StoreRouterConnectingModule } from '@ngrx/router-store';
import { environment } from '../../environments/environment';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { HolidayEffects } from './store/effects/holiday.effect';
import { IgnoreLabelEffects } from './store/effects/ignore-label.effect';
import { ReleaseEffects } from './store/effects/release.effect';
import { WeekdayEffects } from './store/effects/weekday.effect';
import { ProjectEffects } from './store/effects/project.effect';

@NgModule({
  declarations: [],
  imports: [
    StoreModule.forRoot(appReducers),
    EffectsModule.forRoot([
      HolidayEffects, ReleaseEffects, IgnoreLabelEffects, WeekdayEffects, ProjectEffects,
    ]),
    StoreRouterConnectingModule.forRoot({ stateKey: 'router' }),
    !environment.production ? StoreDevtoolsModule.instrument() : [],
  ],
  providers: [TimeCheckerService]
})
export class CoreModule {
}
