import { Injectable, OnDestroy } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { distinctUntilChanged, filter, takeWhile } from 'rxjs/operators';
import isEqual from 'lodash/isEqual';
import { ITokenObj } from '../interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class TokenService implements OnDestroy {

  private token$: BehaviorSubject<ITokenObj> = new BehaviorSubject<ITokenObj>(null);
  private tokenChanged$: BehaviorSubject<ITokenObj> = new BehaviorSubject<ITokenObj>(null);
  private isAlive = true;
  tokenChanged: Observable<ITokenObj> = this.tokenChanged$.asObservable();

  constructor() {
    this.token$.next(this.getToken());

    this.token$
      .pipe(
        distinctUntilChanged(isEqual),
        filter(token => token !== this.tokenChanged$.value),
        takeWhile(() => this.isAlive)
      )
      .subscribe(token => this.tokenChanged$.next(token));
  }

  getToken() {
    return JSON.parse(localStorage.getItem('token')) || null;
  }

  get currentTokenValue(): ITokenObj {
    return this.token$.value;
  }

  setToken(tokenObj: ITokenObj) {
    localStorage.setItem('token', JSON.stringify({ token: tokenObj.token, _id: tokenObj._id }));
    this.token$.next(this.getToken());
  }

  removeToken() {
    localStorage.removeItem('token');
    this.token$.next(null);
  }

  ngOnDestroy() {
    this.isAlive = false;
  }
}
