import { Pipe, PipeTransform } from '@angular/core';

import { TimeCheckerService } from '../../../core';

@Pipe({
  name: 'isChecked'
})
export class IsCheckedPipe implements PipeTransform {

  constructor(private timeCheckerService: TimeCheckerService) {
  }

  transform(value: any, args?: any): any {
    return this.timeCheckerService.workingWeekdays.has(value);
  }
}
