import { Injectable } from '@angular/core';
import * as moment from 'moment';
import * as momentBusinessDays from 'moment-business-time';
import { FormControl } from '@angular/forms';

type formatTime = 'years' | 'months' | 'weeks' | 'days' | 'hours' | 'minutes' | 'seconds';

@Injectable({
    providedIn: 'root'
})
export class TimeCheckerService {

  workingWeekdays: Set<number> = new Set();

  private Holidays = [];

  set holidays(value: any) {
    this.Holidays = value;

    this.updateMomentLocale();
  }

  get holidays() {
    return this.Holidays;
  }

  set setWorkingWeekdays(value: any[]) {
    this.workingWeekdays = new Set(value);

    this.updateMomentLocale();
  }

  constructor() {
    this.updateMomentLocale();
  }

  updateMomentLocale() {
    momentBusinessDays.updateLocale('us', {
      workinghours: {
        0: null,
        1: ['08:00:00', '17:00:00'],
        2: ['08:00:00', '17:00:00'],
        3: ['08:00:00', '17:00:00'],
        4: ['08:00:00', '17:00:00'],
        5: ['08:00:00', '17:00:00'],
        6: null
      },
      holidays: this.holidays,
      holidayFormat: 'MM/DD/YYYY'
    });
  }

  getDiffFullTime(startedDate: string, finishedDate: string): any {
    this.updateMomentLocale();

    // const hours = this.getDiffTime(startedDate, finishedDate);
    const duration = moment.duration(
      momentBusinessDays.utc(finishedDate).workingDiff(momentBusinessDays.utc(startedDate))
    );
    // const days = this.getDiffTime(startedDate, finishedDate, 'days');

    return this.getHours(duration);
  }

  getMaxDate(dates) {
    const momentDate = dates.map((date) => moment(date));

    return moment.max(momentDate);
  }

  getMinDate(dates) {
    const momentDate = dates.map((date) => moment(date));

    return moment.min(momentDate);
  }

  getHours(time): string {

    const seconds = (time / 1000);
    let minutes = parseInt(String(seconds / 60), 10);
    const hours = parseInt(String(minutes / 60), 10);
    minutes = minutes % 60;

    if (hours || minutes) {
      return hours + ':' + (minutes > 9 ? minutes : `0${minutes}`);
    }

    return 'n/a';
  }

}

