import { Component, OnDestroy, OnInit } from '@angular/core';
import { TokenService } from '../core/services/token.service';
import { takeWhile } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { IAppState } from '../core/store/state/app.state';
import { ClearIgnoreLabels, GetIgnoreLabels } from '../core/store/actions/ignore-label.action';
import { ClearHolidays, GetHolidays } from '../core/store/actions/holiday.actions';
import { ClearReleases, GetReleases } from '../core/store/actions/release.actions';
import { ClearWeekdays, GetWeekdays } from '../core/store/actions/weekday.action';
import { ClearProjects, GetProjects } from '../core/store/actions/project.action';
import { Router } from '@angular/router';
import { ITokenObj } from '../core/interfaces/interfaces';


@Component({
  selector: 'app-pivotal-tracker',
  templateUrl: './pivotal-tracker.component.html',
  styleUrls: ['./pivotal-tracker.component.scss']
})
export class PivotalTrackerComponent implements OnInit, OnDestroy {

  tokenObj: ITokenObj;

  private isAlive = true;

  constructor(
    private tokenService: TokenService,
    private store: Store<IAppState>,
    private router: Router
  ) {
  }

  ngOnInit() {
    this.tokenService.tokenChanged
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((token: ITokenObj) => {
        this.tokenObj = token;

        if (this.tokenObj) {
          this.getData();
        } else {
          this.clearData();
        }
    });
  }

  getData() {
    this.store.dispatch(new GetHolidays());
    this.store.dispatch(new GetReleases());
    this.store.dispatch(new GetIgnoreLabels());
    this.store.dispatch(new GetWeekdays());
    this.store.dispatch(new GetProjects(this.tokenObj.token));
  }

  clearData() {
    this.store.dispatch(new ClearHolidays());
    this.store.dispatch(new ClearReleases());
    this.store.dispatch(new ClearIgnoreLabels());
    this.store.dispatch(new ClearWeekdays());
    this.store.dispatch(new ClearProjects());
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }

  removeToken(): void {
    this.tokenService.removeToken();
    this.router.navigate(['/login']);
  }
}
