import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TokenService } from './token.service';
import { environment } from '../../../environments/environment';
import { ITokenObj } from '../interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private url = `${environment.baseURL}/token`;

  constructor(
    private http: HttpClient,
    private tokenService: TokenService,
  ) { }

  saveTokenToDB(token: string) {
    return this.http.post<ITokenObj>(this.url, { token })
      .subscribe(tokenFromBD => {
        this.tokenService.setToken(tokenFromBD);
      });
  }
}
