import { Action } from '@ngrx/store';
import { Project } from '../../interfaces/project';

export enum EProjectActions {
  GetProjects = '[Project] Get Projects',
  GetProjectsSuccess = '[Project] Get Projects Success',
  GetProjectsError = '[Project] Get Projects Error',
  ClearProjects = '[Project] Clear Projects data',
}

export class GetProjects implements Action {
  public readonly type = EProjectActions.GetProjects;
  constructor(public payload: string) {}
}

export class GetProjectsSuccess implements Action {
  public readonly type = EProjectActions.GetProjectsSuccess;
  constructor(public payload: Project[]) {}
}

export class GetProjectsError implements Action {
  public readonly type = EProjectActions.GetProjectsError;
  constructor(public error: any) { }
}

export class ClearProjects implements Action {
  public readonly type = EProjectActions.ClearProjects;
}



export type ProjectActions = GetProjects | GetProjectsSuccess | GetProjectsError | ClearProjects;
