import { createSelector } from '@ngrx/store';

import { IAppState } from '../state/app.state';
import { IWeekdayState } from '../state/weekday.state';

const selectWeekdays = (state: IAppState) => state.weekdays;

export const selectWeekdaysList = createSelector(
  selectWeekdays,
  (state: IWeekdayState) => state.weekdays
);

export const selectWeekdaysListError = createSelector(
  selectWeekdays,
  (state: IWeekdayState) => state.error
);
