import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-archive-cycle-confirmation',
  templateUrl: './archive-cycle-confirmation.component.html',
  styleUrls: ['./archive-cycle-confirmation.component.sass']
})
export class ArchiveCycleConfirmationComponent {
  constructor(
    private dialogRef: MatDialogRef<ArchiveCycleConfirmationComponent>
  ) { }

  closeDialog(answer: boolean) {
    this.dialogRef.close(answer);
  }
}
