import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, map, switchMap } from 'rxjs/operators';
import { EHolidayActions, GetHolidays, GetHolidaysSuccess } from '../actions/holiday.actions';
import { EMPTY } from 'rxjs';
import { HolidaysApiService } from '../../../shared/components/holidays/holidays-api.service';
import { Store } from '@ngrx/store';
import { IAppState } from '../state/app.state';
import { Holiday } from '../../interfaces/holiday';
import { SnackBarErrorComponent } from '../../../shared/components/snack-bar-error/snack-bar-error.component';
import { MatSnackBar } from '@angular/material';
import { HttpErrorResponse } from '@angular/common/http';


@Injectable()
export class HolidayEffects {

  @Effect()
  getHolidays$ = this.actions$.pipe(
    ofType<GetHolidays>(EHolidayActions.GetHolidays),
    // switchMap(() => this.holidaysService.getHolidays()),
    // switchMap((res: Holiday[]) => of(new GetHolidaysSuccess(res)))
    switchMap(() => this.holidaysService.getHolidays().pipe(
      map( (res: Holiday[]) => new GetHolidaysSuccess(res)),
      catchError( (error: HttpErrorResponse) => {
        console.error(error);
        this.snackBar.openFromComponent(SnackBarErrorComponent, { data: 'Can not get Holidays', panelClass: 'snack-error' });
        return EMPTY;
      })
    ))
  );

  constructor(
    private holidaysService: HolidaysApiService,
    private snackBar: MatSnackBar,
    private actions$: Actions,
    private store: Store<IAppState>
  ) {}
}
