import { Action } from '@ngrx/store';
import { Weekday } from '../../interfaces/weekday';

export enum EWeekdayActions {
  GetWeekdays = '[Weekday] Get Weekdays',
  GetWeekdaysSuccess = '[Weekday] Get Weekdays Success',
  GetWeekdaysError = '[Weekday] Get Weekdays Error',
  ClearWeekdays = '[Weekday] Clear Weekdays data',
}

export class GetWeekdays implements Action {
  public readonly type = EWeekdayActions.GetWeekdays;
}

export class GetWeekdaysSuccess implements Action {
  public readonly type = EWeekdayActions.GetWeekdaysSuccess;
  constructor(public payload: Weekday[]) {}
}

export class GetWeekdaysError implements Action {
  public readonly type = EWeekdayActions.GetWeekdaysError;
  constructor(public error: any) { }
}

export class ClearWeekdays implements Action {
  public readonly type = EWeekdayActions.ClearWeekdays;
}

export type WeekdayActions = GetWeekdays | GetWeekdaysSuccess | GetWeekdaysError | ClearWeekdays;
