import { Component } from '@angular/core';
import { MatDialog, MatSnackBar, PageEvent } from '@angular/material';

import { ArchivedCyclesService } from './archived-cycles.service';
import { LoaderService } from '../../core/services/loader';
import { Sort } from '@angular/material';
import { IIteration, ISort } from '../../core/interfaces/interfaces';
import { ArchiveCycleConfirmationComponent } from '../../shared/dialogs/archive-cycle-confirmation/archive-cycle-confirmation.component';
import { filter, map, switchMap, tap } from 'rxjs/operators';
import { ExcelService } from '../../core/services/excel-service';
import { CycleTimeService } from '../cycle-time/cycle-time.service';

@Component({
  selector: 'app-archived-cycles',
  templateUrl: './archived-cycles.component.html',
  styleUrls: ['./archived-cycles.component.sass']
})
export class ArchivedCyclesComponent {
  length;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 25, 50];
  iterations: IIteration[] = [];
  pageIndex = 0;
  sort: ISort = { start: 'desc' };
  displayedColumns: string[] = ['number', 'start', 'finish'];
  private selectedProjectId: number;

  constructor(
    private archivedCyclesService: ArchivedCyclesService,
    private loaderService: LoaderService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private excelService: ExcelService,
    private cycleTimeService: CycleTimeService
  ) { }

  getIterations() {
    this.archivedCyclesService.getArchivedCycles(this.pageSize, this.pageIndex, this.selectedProjectId, this.sort)
      .subscribe(items => this.iterations = items);
  }

  selectProject(projectId: number) {
    this.selectedProjectId = projectId;
    this.getData();
  }

  private getData() {
    this.archivedCyclesService.getArchivedCyclesCount(this.selectedProjectId)
      .subscribe(itemsCount => {
        this.length = itemsCount.count;
        this.pageIndex = 0;
        this.getIterations();
      });
  }

  archiveCycles() {
    const dialogRef = this.dialog.open(ArchiveCycleConfirmationComponent);

    dialogRef.afterClosed()
      .pipe(
        filter(answer => answer),
        tap(() => this.loaderService.showLoader()),
        tap(() => this.snackBar.open('This may take some time. Please, don\'t reload the page', 'Ok')),
        switchMap(() => this.archivedCyclesService.archiveCycles(this.selectedProjectId))
      )
      .subscribe(() => {
        this.getData();
        this.loaderService.hideLoader();
      });
  }

  sortData(sort: Sort) {
    this.sort = { [sort.active]: sort.direction };
    this.getIterations();
  }

  onChangePage(event: PageEvent) {
    const { pageSize, pageIndex, length } = event;
    this.length = length;
    this.pageIndex = pageIndex;
    this.pageSize = pageSize;
    this.getIterations();
  }

  exportToFile() {
    this.loaderService.showLoader();
    this.archivedCyclesService.getStoriesByProjectId(this.selectedProjectId)
      .pipe(
        map(items => this.cycleTimeService.parseReleaseStory(items))
      )
      .subscribe(items => {
        this.excelService.exportAsExcelFile(items, `Archived cycles - Project ID: ${this.selectedProjectId}`);
        this.loaderService.hideLoader();
      });
  }
}
