import { Component, OnInit } from '@angular/core';
import { StoryActivityService } from '../../../features/story-activity/story-activity.service';
import { AuthService } from '../../../core/services/auth.service';
import { Router } from '@angular/router';
import { ITokenObj } from '../../../core/interfaces/interfaces';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.sass']
})
export class LoginComponent implements OnInit {

  tokenObj: ITokenObj;

  constructor(
    private storyActivityService: StoryActivityService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  getProjects(value: string): void {
    if (!value) {
      this.storyActivityService.openDialog();
      return;
    }

    this.storyActivityService.getProjects(value)
      .subscribe(() => {
        if (!this.tokenObj || this.tokenObj.token !== value) {
          this.authService.saveTokenToDB(value);
        }
        this.router.navigate(['/pivotal/cycle-time']);
      });
  }
}
