import { createSelector } from '@ngrx/store';

import { IAppState } from '../state/app.state';
import { IReleaseState } from '../state/release.state';

const selectReleases = (state: IAppState) => state.releases;

export const selectReleasesList = createSelector(
  selectReleases,
  (state: IReleaseState) => state.releases
);

export const selectReleasesListError = createSelector(
  selectReleases,
  (state: IReleaseState) => state.error
);
