import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { TokenService } from '../../core/services/token.service';
import { IArchivedStory, IIteration, IReview, ISort, IStory } from '../../core/interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class ArchivedCyclesService {
  private url = `${environment.baseURL}/iteration`;
  private storyUrl = `${environment.baseURL}/story`;
  private reviewUrl = `${environment.baseURL}/review`;

  constructor(
    private http: HttpClient,
    private tokenService: TokenService
  ) {
  }

  private reviewForStoryUrl(storyId) {
    return `${environment.baseURL}/story/${storyId}/reviews`;
  }

  getArchivedCycles(pageSize: number, pageIndex: number, projectId: number, sort: ISort): Observable<IIteration[]> {
    const params = new HttpParams({
      fromObject: {
        offset: String(pageSize * pageIndex),
        limit: String(pageSize),
        sort: JSON.stringify(sort),
        project_id: projectId.toString()
      }
    });

    return this.http.get<IIteration[]>(this.url, { params });
  }

  getStoriesByProjectId(projectId: number): Observable<IArchivedStory[]> {
    return this.http.get<IArchivedStory[]>(`${this.storyUrl}/project/${projectId}`);
  }

  getArchivedCyclesCount(projectId: number): Observable<{ count: number }> {
    const params = new HttpParams({
      fromObject: { project_id: projectId.toString() }
    });

    return this.http.get<{ count: number }>(`${this.url}/count`, { params });
  }

  getById(id: string): Observable<IIteration> {
    return this.http.get<IIteration>(`${this.url}/${id}`);
  }

  archiveCycles(projectId): Observable<any> {
    return this.http.post<any>(this.url, {
      tokenObj: this.tokenService.currentTokenValue,
      projectId
    });
  }

  archiveReviewsForStories(storyIds: number[], projectId: number) {
    return this.http.post(this.reviewUrl, {
      storyIds,
      projectId,
      tokenObj: this.tokenService.currentTokenValue
    });
  }

  getReviewsForStories(storyId: number) {
    return this.http.get<IReview[]>(this.reviewForStoryUrl(storyId));
  }

  getStoriesByReleaseLabel(label: string): Observable<IStory[]> {
    return this.http.get<IStory[]>(`${this.url}/release/${label}`);
  }
}
