import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { catchError, map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { Store } from '@ngrx/store';
import { IAppState } from '../state/app.state';
import { EReleaseActions, GetReleases, GetReleasesError, GetReleasesSuccess } from '../actions/release.actions';
import { Release } from '../../interfaces/release';
import { ReleaseApiService } from '../../../shared/components/releases/release-api.service';


@Injectable()
export class ReleaseEffects {

  @Effect()
  getReleases$ = this.actions$.pipe(
    ofType<GetReleases>(EReleaseActions.GetReleases),
    switchMap(() => this.releasesService.getReleases().pipe(
      map( (res: Release[]) => new GetReleasesSuccess(res)),
      catchError( (er: any) => of(new GetReleasesError(er)))
    ))
  );

  constructor(
    private releasesService: ReleaseApiService,
    private actions$: Actions,
    private store: Store<IAppState>
  ) {}
}
