import { Injectable } from '@angular/core';
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';
import { DatePipe } from '@angular/common';

const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';

@Injectable({
  providedIn: 'root'
})
export class ExcelService {

  constructor(private datePipe: DatePipe,) {
  }

  exportAsExcelFile(json: any[], excelFileName: string): void {
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    const workbook: XLSX.WorkBook = {Sheets: {data: worksheet}, SheetNames: ['data']};
    const excelBuffer: any = XLSX.write(workbook, {bookType: 'xlsx', type: 'array'});

    this.saveAsExcelFile(excelBuffer, excelFileName);
  }

  private saveAsExcelFile(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], {
      type: EXCEL_TYPE
    });

    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
  }

  getCycle_TimeDetails({kind, story_id, ...rest}) {
    return rest;
  }

  exportAsRawFile(iterations: any[], excelFileName: string): void {

    const workbook: XLSX.WorkBook = XLSX.utils.book_new();

    iterations.forEach((iteration, i) => {
      const arr = [...iteration.stories];
      const newArray = arr.map(a => ({...a}));

      const newArr = newArray.map((story) => {

        const res = {
          State: story.current_state,
          Type: story.story_type,
          Estimate: story.estimate,
          Name: story.name,
          'Story ID': story.cycle_time_details.story_id,
          Labels: story.labels.map(label => label.name).join(', '),
          Owners: story.owners.map(owner => owner.name).join(', '),
          ...this.getCycle_TimeDetails(story.cycle_time_details)
        };

        return res;
      });


      const worksheetItem: XLSX.WorkSheet = XLSX.utils.json_to_sheet(newArr);
      const wsName = this.datePipe.transform(iteration.start, 'dd-MM-yyyy') + '__' + this.datePipe.transform(iteration.finish, 'dd-MM-yyyy');
      XLSX.utils.book_append_sheet(workbook, worksheetItem, wsName);
    });


    const excelBuffer: any = XLSX.write(workbook, {bookType: 'xlsx', type: 'array'});

    this.saveAsExcelFile(excelBuffer, excelFileName);
  }

}
