import { Injectable, Injector, OnInit } from '@angular/core';

import { PivotalTrackerService } from '../pivotal-tracker.service';
import { select, Store } from '@ngrx/store';
import { IAppState } from '../../core/store/state/app.state';
import { TimeCheckerService } from '../../core/services/time-checker';
import * as moment from 'moment';
import { CycleTime } from '../../shared/enums';
import { IArchivedStory, IStory } from '../../core/interfaces/interfaces';
import { selectReleasesList } from '../../core/store/selectors/release.selector';
import { Release } from '../../core/interfaces/release';

@Injectable()
export class CycleTimeService extends PivotalTrackerService implements OnInit {
  activityEnum = CycleTime;
  getReleasesList$ = this.store.pipe(select(selectReleasesList));
  releases: Release[] = [];

  constructor(
    protected injector: Injector,
    protected store: Store<IAppState>,
    private timeCheckerService: TimeCheckerService
  ) {
    super(injector, store);

    super.setIterationsSubUrl('/iterations?fields=number,project_id,start,finish,stories(id,created_at,accepted_at,story_type,updated_at,name,description,current_state,url,project_id,owner_ids,labels,owned_by_id,cycle_time_details)&limit=50&offset=-49&scope=done_current'); //tslint:disable-line
    super.setIterationsSecondSubUrl('/iterations?fields=number,project_id,start,finish,stories(id,created_at,accepted_at,story_type,updated_at,name,description,current_state,url,project_id,owner_ids,labels,owned_by_id,cycle_time_details)&limit=50&offset=-99&scope=done_current'); //tslint:disable-line
  }

  ngOnInit(): void {
    this.getReleasesList$.subscribe(data => this.releases = data);
  }

  parseStories(stories: IStory[], iterationStart: string): { [key: string]: string }[] {
    return stories
      .map((story) => {
        return {
          [this.activityEnum.projectId]: String(story.project_id && story.project_id),
          [this.activityEnum.storyId]: String(story.id),
          [this.activityEnum.storyName]: story.name,
          [this.activityEnum.storyType]: story.story_type,
          [this.activityEnum.labels]: story.labels.map((item) => item.name).join(', ') || 'n/a',
          [this.activityEnum.owner]: this.parseOwners(story.owners),
          [this.activityEnum.release]: this.getReleaseName(story.labels),
          [this.activityEnum.deliveredTime]: this.getDeliveredTime(story, iterationStart),
          [this.activityEnum.deliveredCount]: String(story.cycle_time_details.delivered_count),
          [this.activityEnum.finishedTime]: this.timeCheckerService.getHours(story.cycle_time_details.finished_time),
          [this.activityEnum.finishedCount]: String(story.cycle_time_details.finished_count),
          [this.activityEnum.rejectedCount]: this.timeCheckerService.getHours(story.cycle_time_details.rejected_time),
          [this.activityEnum.rejectedTime]: String(story.cycle_time_details.rejected_count),
          [this.activityEnum.startedTime]: this.timeCheckerService.getHours(story.cycle_time_details.started_time),
          [this.activityEnum.startedCount]: String(story.cycle_time_details.started_count),
          [this.activityEnum.totalCycleTime]: this.timeCheckerService.getHours(story.cycle_time_details.total_cycle_time),
          [this.activityEnum.qualityAssurance]: this.getQualityAssurance(story.labels)
        };
      });
  }

  parseReleaseStory(stories: IArchivedStory[]): { [key: string]: string }[] {
    return stories.map(story => ({
      [this.activityEnum.sprintNumber]: String(story.iteration.number),
      [this.activityEnum.projectId]: String(story.project_id),
      [this.activityEnum.storyId]: String(story.id),
      [this.activityEnum.storyName]: story.name,
      [this.activityEnum.storyType]: story.story_type,
      [this.activityEnum.labels]: story.labels.map((item) => item.name).join(', ') || 'n/a',
      [this.activityEnum.owner]: this.parseOwners(story.owners),
      [this.activityEnum.release]: this.getReleaseName(story.labels),
      [this.activityEnum.deliveredTime]: this.getDeliveredTime(story, story.iteration.start),
      [this.activityEnum.deliveredCount]: String(story.cycle_time_details.delivered_count),
      [this.activityEnum.finishedTime]: this.timeCheckerService.getHours(story.cycle_time_details.finished_time),
      [this.activityEnum.finishedCount]: String(story.cycle_time_details.finished_count),
      [this.activityEnum.rejectedCount]: this.timeCheckerService.getHours(story.cycle_time_details.rejected_time),
      [this.activityEnum.rejectedTime]: String(story.cycle_time_details.rejected_count),
      [this.activityEnum.startedTime]: this.timeCheckerService.getHours(story.cycle_time_details.started_time),
      [this.activityEnum.startedCount]: String(story.cycle_time_details.started_count),
      [this.activityEnum.totalCycleTime]: this.timeCheckerService.getHours(story.cycle_time_details.total_cycle_time),
      [this.activityEnum.qualityAssurance]: this.getQualityAssurance(story.labels)
    }));
  }

  filterStoriesByLabel(stories: IStory[], ignoreLabels: string[]): IStory[] {
    return stories.filter(
      (story: IStory) => !story.labels.some(label => ignoreLabels.includes(label.name))
    );
  }

  private parseOwners(owners: any[]): string {
    return owners && owners.map(item => item.name).join(', ') || 'n/a';
  }

  private getDeliveredTime(story, iterationStartDate) {
    const { accepted_at } = story;
    const { delivered_time } = story.cycle_time_details;
    const storyRelease = this.getReleaseName(story.labels);
    const release = this.releases.find(release => release.name === storyRelease);

    if (delivered_time === 0) {
      return 'n/a';
    }

    if (accepted_at) {
      return this.calculateAcceptedStoryDeliveredTime(story.cycle_time_details, release, accepted_at);
    } else {
      return this.calculateNotAcceptedStoryDeliveredTime(story.cycle_time_details, release);
    }
  }

  private calculateNotAcceptedStoryDeliveredTime(cycle_time_details, release) {
    const {
      total_cycle_time,
      finished_time,
      started_time,
      delivered_time
    } = cycle_time_details;

    const storyCreatedDate = moment.utc().subtract(total_cycle_time);
    let deliveredStartDate = moment.utc(storyCreatedDate).add(started_time, finished_time);

    if (release && deliveredStartDate.isBefore(release.date)) {
      deliveredStartDate = moment.utc(release.date);
    }

    const deliveredEndDate = moment.utc(deliveredStartDate.format()).add(delivered_time);
    return this.timeCheckerService.getDiffFullTime(deliveredStartDate.format(), deliveredEndDate.format());
  }

  private calculateAcceptedStoryDeliveredTime(cycle_time_details, release, accepted_at) {
    const { delivered_time, rejected_time } = cycle_time_details;

    let deliveredStartDate = moment.utc(accepted_at).subtract(delivered_time + rejected_time);

    if (release && deliveredStartDate.isBefore(release.date)) {
      deliveredStartDate = moment.utc(release.date);
    }

    const deliveredEndDate = moment.utc(accepted_at).subtract(rejected_time);
    return this.timeCheckerService.getDiffFullTime(deliveredStartDate.format(), deliveredEndDate.format());
  }

  private getQualityAssurance(labels) {
    const mark = 'qahrs_';
    const label = labels.find(item => item.name.includes(mark));
    return label ? label.name.replace(mark, '') : 'n/a';
  }

  private getReleaseName(labels) {
    const result = labels.find(item => item.name.match(/[0-9]{2}r[0-9]{2}$/));
    return result ? result.name : 'n/a';
  }
}
