import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { selectProjectsList } from '../../../core/store/selectors/project.selector';
import { IAppState } from '../../../core/store/state/app.state';
import { takeWhile } from 'rxjs/operators';
import { Project } from '../../../core/interfaces/project';
import { LocalStorageService } from '../../../core/services/local-storage/local-storage.service';

@Component({
  selector: 'app-projects-selector',
  templateUrl: './projects-selector.component.html',
  styleUrls: ['./projects-selector.component.sass']
})
export class ProjectsSelectorComponent implements OnInit, OnDestroy {
  @Output() onSelect = new EventEmitter<number>();

  getProjectsList$ = this.store.pipe(select(selectProjectsList));
  projects: Project[] = [];
  selectedProject: number;

  private isAlive = true;

  constructor(
    private store: Store<IAppState>,
    private localStorageService: LocalStorageService
  ) { }

  ngOnInit() {
    this.getProjectsList$
      .pipe(takeWhile(() => this.isAlive))
      .subscribe((projects: Project[]) => {
        this.selectedProject = this.localStorageService.getProject();
        this.projects = projects;

        if (this.selectedProject) {
          this.selectProject(this.selectedProject);
        }
      });
  }

  selectProject(value: number) {
    this.localStorageService.setProject(value);
    this.selectedProject = value;
    this.onSelect.emit(value);
  }

  ngOnDestroy(): void {
    this.isAlive = false;
  }
}
