import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { Weekday } from '../../../core/interfaces/weekday';
import { Holiday } from '../../../core/interfaces/holiday';

@Injectable({
  providedIn: 'root'
})
export class HolidaysApiService {
  private weekdayUrl = `${environment.baseURL}/weekday`;
  private holidayUrl = `${environment.baseURL}/holiday`;

  constructor(
    private http: HttpClient,
  ) { }

  getWeekdays(): Observable<any> {
    return this.http.get<any>(this.weekdayUrl);
  }

  updateWeekdays(weekdays: Weekday[]): Observable<any> {
    return this.http.put<any>(this.weekdayUrl, weekdays);
  }

  getHolidays(): Observable<any> {
    return this.http.get<any>(this.holidayUrl);
  }

  addHoliday(holiday: Holiday): Observable<any> {
    return this.http.post<any>(this.holidayUrl, holiday);
  }

  updateHoliday(holiday: Holiday): Observable<any> {
    return this.http.put<any>(this.holidayUrl, holiday);
  }

  deleteHoliday(holidayId: string): Observable<any> {
    return this.http.delete<any>(`${this.holidayUrl}/${holidayId}`);
  }

}
