import { createSelector } from '@ngrx/store';

import { IAppState } from '../state/app.state';
import { IignoreLabelState } from '../state/ignore-label.state';

const selectIgnoreLabels = (state: IAppState) => state.ignoreLabels;

export const selectIgnoreLabelsList = createSelector(
  selectIgnoreLabels,
  (state: IignoreLabelState) => state.ignoreLabels
);

export const selectIgnoreLabelsListError = createSelector(
  selectIgnoreLabels,
  (state: IignoreLabelState) => state.error
);
